"use client"

import { useEffect, useRef } from "react"
import { buildGraph } from "../lib/graphTransform"
import { ForensicResponse } from "../types/forensic"

export default function GraphView({
  data,
  suspiciousOnly,
  highlightedAccount,
  onNodeClick
}: {
  data: ForensicResponse
  suspiciousOnly: boolean
  highlightedAccount: string | null
  onNodeClick: (id: string) => void
}) {
  const containerRef = useRef<HTMLDivElement>(null)
  const rendererRef = useRef<any>(null)

  useEffect(() => {
    if (!containerRef.current) return

    let mounted = true

    import("sigma").then((SigmaModule) => {
      if (!mounted) return

      const Sigma = SigmaModule.default

      // ðŸ”¥ Kill previous renderer properly
      if (rendererRef.current) {
        rendererRef.current.kill()
        rendererRef.current = null
      }

      const graph = buildGraph(data, suspiciousOnly, highlightedAccount)

      const renderer = new Sigma(graph, containerRef.current!)
      rendererRef.current = renderer

      renderer.on("clickNode", (event: any) => {
        onNodeClick(event.node)
      })

      renderer.getCamera().animatedReset()
    })

    return () => {
      mounted = false

      if (rendererRef.current) {
        rendererRef.current.kill()
        rendererRef.current = null
      }
    }
  }, [data, suspiciousOnly, highlightedAccount])

  return (
    <div
      ref={containerRef}
      style={{
        height: "600px",
        width: "100%",
        background: "white",
        border: "1px solid #ddd"
      }}
    />
  )
}
