import Graph from "graphology"
import { ForensicResponse } from "../types/forensic"

export function buildGraph(
  data: ForensicResponse,
  suspiciousOnly: boolean,
  highlightedAccount: string | null
) {
  const graph = new Graph()

  const suspiciousMap = new Map(
    data.suspicious_accounts.map(a => [a.account_id, a])
  )

  const accounts = new Set<string>()

  data.fraud_rings.forEach(ring => {
    ring.member_accounts.forEach(a => {
      if (!suspiciousOnly || suspiciousMap.has(a)) {
        accounts.add(a)
      }
    })
  })

  const list = Array.from(accounts)
  const radius = 10

  list.forEach((account, i) => {
    const angle = (i / list.length) * 2 * Math.PI
    const suspicion = suspiciousMap.get(account)?.suspicion_score || 0

    const isHighlighted = highlightedAccount === account

    graph.addNode(account, {
      label: account,
      size: isHighlighted ? 15 : 6 + suspicion / 10,
      color: isHighlighted
        ? "#0066ff"
        : getColor(suspicion),
      x: Math.cos(angle) * radius,
      y: Math.sin(angle) * radius
    })
  })

  data.fraud_rings.forEach(ring => {
    for (let i = 0; i < ring.member_accounts.length; i++) {
      const a = ring.member_accounts[i]
      const b = ring.member_accounts[(i + 1) % ring.member_accounts.length]

      if (graph.hasNode(a) && graph.hasNode(b)) {
        graph.addEdge(a, b, {
          color:
            highlightedAccount &&
            (a === highlightedAccount || b === highlightedAccount)
              ? "#0066ff"
              : "#ccc"
        })
      }
    }
  })

  return graph
}

function getColor(score: number) {
  if (score > 80) return "#ff0000"
  if (score > 60) return "#ff6600"
  if (score > 40) return "#ffaa00"
  return "#ffd700"
}
